.slider {
	user-select: text;
}


 .scrollup {
	width: 37px;
	height: 84px;
	position: fixed;
	bottom: 50px;
	right: 15px;
	display: none;
	text-indent: -9999px;
	background: url('../images/arrow-top.png') no-repeat;
	z-index: 100;
	@media @tablet {
		right: 100px;
	}
} 


.video-responsive { 
	position: relative; 
	padding-bottom: 56.25%; 
	height: 0; 
	overflow:hidden; 
} 
.video-responsive iframe, 
.video-responsive object, 
.video-responsive embed {
	position: absolute; 
	top: 0; 
	left: 0; 
	width: 100%;
	height: 100%; 
}



