.header {
  position: relative;
  background: url(../images/header__hero.jpg) no-repeat center / cover;
}
.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 658px;
  height: 652px;
  background: url('../images/decor/header__decor-left.png');
}
.header:after {
  content: '';
  position: absolute;
  bottom: -170px;
  right: 0;
  width: 830px;
  height: 1107px;
  background: url('../images/decor/header__decor-right.png');
}
.header__top {
  position: relative;
  padding: 12px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}
.header__logo {
  display: flex;
  align-items: center;
  flex-basis: 530px;
}
.header__title {
  padding-left: 15px;
}
.header__tel {
  display: flex;
  align-items: center;
  font-weight: 500;
}
.header .ico_mobile {
  margin-right: 15px;
}
.siteInfo {
  position: relative;
  display: flex;
  justify-content: space-between;
  z-index: 10;
}
.siteInfo__left {
  padding-top: 260px;
}
.siteInfo__right {
  position: relative;
  padding-top: 80px;
  margin-right: -220px;
}
.siteInfo__title {
  position: relative;
  padding-bottom: 8px;
  font-size: 40px;
  font-weight: 900;
}
.siteInfo__title:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 98%;
  height: 2px;
  background: #fff;
}
.siteInfo__title-accent {
  font-size: 46.5px;
}
.siteInfo__slogan {
  font-size: 39px;
}
.siteInfo__listTitle {
  padding-top: 48px;
  font-size: 31.23px;
  font-weight: 700;
}
.siteInfo__list {
  padding-top: 15px;
}
.siteInfo__item {
  font-size: 21.83px;
  list-style: disc inside;
  vertical-align: middle;
}
.siteInfo__guarantee {
  position: relative;
  font-size: 56.7px;
  line-height: 1;
  font-weight: 700;
}
.siteInfo__guarantee:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
}
.siteInfo__guarantee-accent {
  position: relative;
  display: block;
  font-size: 35px;
  font-weight: 400;
}
.siteInfo__guarantee-accent:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
}
.siteInfo .guarantee__text {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, 95%);
}
