  border: none;
  width: 100%;
  height: 100%;
}
.thanks__title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}
.thanks__text {
  display: block;
  padding-bottom: 15px;
  padding-left: 0;
  font-size: 18px;
  color: #000;
}
@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes antiSmoothScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
@keyframes growLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  top: 50%;
  transform: translateY(-50%);
  font-family: 'Fontello';
  transition: all .3s linear;
  z-index: 10;
}
.slick-prev {
  left: -40px;
}
.slick-next {
  right: -40px;
}
.slick-prev:before,
.slick-next:before {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  color: #fff;
  font-size: 45px;
}
.slick-prev:before {
  left: 20px;
  content: '\e805';
}
.slick-next:before {
  content: '\e806';
  right: 10px;
}
.slick-dots {
  left: 50%;
  bottom: -40px;
  transform: translateX(-50%);
  border-image: transparent;
}
.slick-dots li {
  margin: 0 5px;
  width: 20px;
  height: 20px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 20px;
  width: 20px;
  font-size: 0px;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
